export default class amazonRename {
  static rename(url) {
    return url.replace(
      'https://s3-us-west-1.amazonaws.com/screenshots.templatemonster.com/',
      'https://s3.tmimgcdn.com/',
    ).replace(
      'https://s3.us-west-1.amazonaws.com/screenshots.templatemonster.com/',
      'https://s3.tmimgcdn.com/',
    ).replace(
      'https://s3-us-west-1.amazonaws.com/tmuploader/',
      'https://s3u.tmimgcdn.com/',
    ).replace(
      'https://s3.us-west-1.amazonaws.com/tmuploader/',
      'https://s3u.tmimgcdn.com/',
    );
  }
  static convert(url, obj) {
    if (url === null || typeof url === 'undefined' || url.trim().length === 0) return url;
    let list = [];
    if (obj.w !== undefined) list.push(`width=${obj.w}`);
    if (obj.h !== undefined) list.push(`height=${obj.h}`);
    list = list.length > 0 ? `?${list.join('&')}` : '';
    return `${amazonRename.rename(url)}${list}`;
  }
}
